import Nav from "./components/Nav";
import Homepage from "./views/Homepage";
import Login from "./views/Login";
import User from "./views/User";
import Footer from "./components/Footer";
import "./App.css";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faUserCircle, faSignOutAlt } from "@fortawesome/free-solid-svg-icons";
import { Redirect, Router, Switch, Route } from "react-router-dom";
import { useSelector } from "react-redux";

import { customHistory } from "./helper/history";

//
library.add(faUserCircle, faSignOutAlt);

function App() {
  const state = useSelector((state) => state.authentication);
  return (
    <Router history={customHistory}>
      <header>
        <Nav />
      </header>
      <Switch>
        <Route exact path="/p10_BankAPI/">
          <Homepage />
        </Route>
        <Route path="/p10_BankAPI/login">
          <Login />
        </Route>
        <Route path="/p10_BankAPI/profile">
          {state.isAuthenticated ? <User /> : <Redirect to="/p10_BankAPI/" />}
        </Route>
      </Switch>
      <Footer />
    </Router>
  );
}

export default App;
